<template>
  <div class="page-post">

    <mt-header title="">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <div class="post-info">
      <mix-box 
        image="http://www.cssoul.com/demo/weihai/company/6.jpg" 
        title="" 
        salary="3500-5000" 
        source=" McDonald's">
      </mix-box>
      <div class="post-info-foot">
        <span></span>
        <span></span>
        <span></span>
      </div>
    </div>
    
    <div class="box box-loc">
      <common-title iconClass="icon-loc" text="371"> </common-title>
    </div>

    <div class="box">
      <common-title iconClass="icon-money" text=""> </common-title>
      <div class="box-desc">
        
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-info" text=""> </common-title>
      <div class="box-desc">
        <p>
          1

        </p>
        <p>
          2
        </p>
        <p>
          3
        </p>
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-welfare-blue" text=""> </common-title>
      <div class="box-desc">
        <p>135004000400</p>
        <p>210%55%70</p>
        <p>3</p>
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-condition" text=""> </common-title>
      <div class="box-desc">
        <p>1</p>
        <p>220</p>
        <p>3</p>
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-attention" text=""> </common-title>
      <div class="box-desc">
        
      </div>
    </div>

    <div class="fix-bottom fix-post">
      <mt-button @click.native="handleAsk">
        <img src="../../images/consultation_online_icon@3x.png" height="20" width="20" slot="icon">
        
      </mt-button>
      <mt-button @click.native="handleTell">
        <img src="../../images/consultation_call_icon@3x.png" height="20" width="20" slot="icon">
        
      </mt-button>
      <mt-button @click.native="handleApply" type="primary">
        
      </mt-button>
    </div>  

</div>
</template>

<script>
import commonTitle from '../common/title.vue'
import mixBox from '../common/mixBox.vue'

export default {
  name: 'page-post',

  components: {
      commonTitle,
      mixBox
  },
  methods: {
    handleAsk: function() {
      this.$messagebox({
        title: '',
        message: '',
        type:'alert',
        showCancelButton: false,
        confirmButtonText: ''
      }).then(action => {
        console.log(action);
      });
    },
    handleTell: function() {
      location.href = 'tel: 18329169978';
    },
    handleApply: function() {

      this.$messagebox({
        title: '',
        message: '',
        type:'confirm',
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: '',
      }).then(action => {
        if(action == "confirm"){
          this.$router.push({ path: 'user/login', query: { refer: 'post' }})
        }else{
          console.log(action);
        }
      });

    }
  }
};
</script>

<style lang="css">
  .page-post{
    background:#f0f0f0;
    padding-bottom:50px;
  }

  .post-info{
    padding:20px 15px;
    background:#fff;
  }
  .post-info-foot{
    margin-top:15px;
  }
  .post-info-foot span{
    display: inline-block;
    margin-right:12px;
    font-size: 14px;
    padding: 0 12px;
    height: 33px;
    line-height:33px;
    border: 1px solid #D7D7D7;
    border-radius:4px;
    background:#fff;
    color: #828282;
  }

  .box-loc{
    border-bottom:none;
  }

  .fix-post{
    display:flex;
  }
  .fix-post .mint-button--normal{
    flex: 1;
    background:#fff;
    font-size:14px;
    
    box-shadow:none;
    font-size:13px;
  }
  .fix-post .mint-button:first-child{
    border-right:1px solid #E9E9E9;
  }
  .fix-post .mint-button--normal label{
    display:block;
  }
  .fix-post .mint-button--primary{
    flex: 2;
    background: #0977C5;
    box-shadow:none;
    font-size:16px;
  }
</style>



